import { Controller, Selection, Block, Queries } from '@ali/4ever-cangjie';

const getRangeFromNodes = (controller: Controller, nodes: Block[]) => {
  const startBlock = nodes[0];
  const endBlock = nodes[nodes.length - 1];
  const anchor = controller.query(Queries.pointAtStartOfNode, startBlock);
  const focus = controller.query(Queries.pointAtEndOfNode, endBlock);
  const selection = Selection.create({ anchor, focus });

  return selection;
};

export default getRangeFromNodes;
